import {Info} from "./info.js";
export class Book extends Info {
	constructor(title,subtitle,author,language,has_fulltext,first_publish_year,publish_year,key,isRead=false){
		super(title,subtitle,language,has_fulltext,first_publish_year,publish_year);
		this.author = author;
		this.isRead = isRead;
		this.has_fulltext = has_fulltext ? "yes" : "no";
		this.key = key;
		for(let key in this){
			if(this[key]=== undefined) this[key] = "";
		}
	}
}