export class Ui {
	constructor(searchInput,searchButton,list,countFound,countStart,countSize,info,next,prev,toReadList,pushButton,readCountAll,readCountRead){
		this.searchInput = searchInput;
		this.searchButton= searchButton;
		this.list = list;
		this.countFound = countFound;
		this.countStart = countStart;
		this.countSize = countSize;
		this.info = Object.assign(info);
		this.toReadList = toReadList;
		this.pushButton = pushButton;
		this.next = next;
		this.prev = prev;
		this.readCountAll = readCountAll;
		this.readCountRead = readCountRead;
		this._byId();
	}
	_byId() {
		for(let key in this){
			if(typeof this[key]==="object"){
				this._byId.bind(this[key])();
			}
			else {
				this[key] = document.getElementById(this[key]);
			}
		}
	}
	showCounter(start,size) {
		this.countFound.innerText = size;
		this.countStart.innerText = start;
		this.countSize.innerText = 100;
	}
	readListTemplate(obj) { //вынести в ui
		return `
				<div class="rightBlock-list__item ${obj.isRead?"mark":""}">
				<p class="rightBlock-list__item-title">
					${obj.title}(${obj.language})
				</p>
				<p class="rightBlock-list__item-subtitle">
					${obj.subtitle}	
				</p>
				<p class="rightBlock-list__item-author">
					${obj.author}
				</p>
				<div class="rightBlock-list__item-buttons">
					<a class="rightBlock-list__item-mark">Mark as read</a>
					<a class="rightBlock-list__item-remove">Remove from list</a>
				</div>
			</div>
	`;		
	}

	listTemplate(obj) {
		return	`
		<div class="leftBlock__item">
			<p class="leftBlock__item-title">
					${obj.title}(${obj.language})
			</p>
			<p class="leftBlock__item-subtitle">
				${obj.subtitle}
			<p>
		</div>
		`;
	}
}


