import 'regenerator-runtime/runtime';
import {Ui} from "./ui.js";
//import {isInLocalStorage,addToLocalStorage,loadFromLocalStorage,deleteFromLocalStorage,changeLocalStorage} from "./localStorage.js";
import {Book} from "./book.js";
//import {Counter} from "./counter.js";
import {loadData} from "./api.js";
import {Active} from "./active.js";

class Counter {
	constructor(start,size)
	{
		this.start = start;
		this.size = size;
		ui.showCounter(this.start,this.size);
	}
	next() {
		if((this.start+1)*100<this.size)
		{
			this.start++;
			ui.showCounter(this.start,this.size);
			return true;
		}
		return false;
	}
	prev() {
		if(this.start-1>=1)
		{
			this.start--;
			ui.showCounter(this.start,this.size);
			return true;
		}
		return false;
	}
}
function isInLocalStorage(obj) {
	for(let k in localStorage)
	{
		if(obj.key == k) return true;
	}
	return false;
}
function addToLocalStorage(obj){
	localStorage.setItem(obj.key,JSON.stringify(obj));
}

function loadFromLocalStorage()
{
	let prefix = "/works/";
	let data = {docs:[]};
	for(let key in localStorage)
	{
		if(key.includes(prefix))
			data.docs.push(JSON.parse(localStorage[key]));
	}
	let savedData = saveData(data);
	for(let item of savedData.docs)
	{
		addToReadList(item);
	}
	return data.docs;
}	
function deleteFromLocalStorage(key)
{
	localStorage.removeItem(key);
}
function changeLocalStorage(key,obj)
{
	//console.log(obj);
	localStorage.setItem(key,JSON.stringify(obj));
}


function saveData(arr)
{
	let data = {
		start:arr.start,
		num_found:arr.num_found,
	};
	data.docs = [];
	for(let item of arr.docs)
	{
		data.docs.push(new Book(item.title,
			item.subtitle,
			item.author_name,
			item.language,
			item.has_fulltext,
			item.first_publish_year,
			item.publish_year,
			item.key,
			item.isRead));
	}
	return data;
}
function createList(arr){
	let list = "";
	for(let item of arr)
	{
		list += ui.listTemplate(item);
	}	

	return list;
}
function addToList(data){
	let block = ui.countFound.parentElement.parentElement.parentElement;
	[...block.children].forEach(item => item.setAttribute("class",item.className+" visible"))
	//if(ui.countFound !== undefined) block.setAttribute('class',block.className + " visible");
	ui.list.innerHTML = data;
}
function addToReadList(obj)
{
	ui.toReadList.innerHTML += ui.readListTemplate(obj);
}

function searchButtonHandler(start) {
 loadData(start).then(res=>{
 	booksData = saveData(res);
 	addToList(createList(booksData.docs));
 	if(counter==null) counter = new Counter(start,booksData.num_found);
 })
}
function listHandler(e) {
	let noActive = "leftBlock__item";
	if(active != null){
		active.target.setAttribute("class",noActive);
	}
	active = new Active();
	if(e.target.className === noActive) active.target = e.target;
	else if(e.target.parentElement.className == noActive) active.target = e.target.parentElement;

	active.target.setAttribute("class",noActive + " active");
	let index = [...active.target.parentElement.children].indexOf(active.target);
	active.book = booksData.docs[index];
	for(let key in ui.info)
	{
		ui.info[key].innerHTML = active.book[key]; 
	}
	let block = ui.info.title.parentElement.parentElement;
	if(!block.className.includes("visible"))
	{
		block.setAttribute("class",block.className + " visible");
	}
}


function counterHandler(count)
{

	if(counter==null) return;
	if(count > 0){
		if(counter.next())
			searchButtonHandler(counter.start);
	}
	else {
		if(counter.prev())
			searchButtonHandler(counter.start);
	}
}

function pushButtonHandler()
{
	if(!isInLocalStorage(active.book))
	{
		addToReadList(active.book);
		addToLocalStorage(active.book);
		readList.push(active.book);
		ui.readCountAll.innerText++;
	}
	else alert("Book is already in the list");
}
function removeButtonHandler(e) {
	let listItem = e.target.parentElement.parentElement;
	let index = [...listItem.parentElement.children].indexOf(listItem);
	listItem.parentElement.removeChild(listItem);
	deleteFromLocalStorage(readList[index].key);
	readList.splice(index,1);
	ui.readCountAll.innerText--;
}

function markButtonHander(e)
{
	console.log();
	const className = "rightBlock-list__item";
	let listItem = e.target.parentElement.parentElement;
	let index = [...listItem.parentElement.children].indexOf(listItem);
	//console.log(readList[index].isRead = true);
	readList[index].isRead = true;
	listItem.removeChild(e.target.parentElement);
	listItem.setAttribute("class",className + " mark");
	//console.log(readList[index]);
	changeLocalStorage(readList[index].key,readList[index]);
	ui.readCountRead.innerText++;
}




let info = {
	title :"infoTitle",
	subtitle :"infoSubtitle",
	language :"infoLang",
	has_fulltext : "infoFullText",
	first_publish_year : "infoFirstPublish",
	publish_year : "infoYears"
}

let ui = new Ui("searchInput","searchButton","list","countFound","countStart","countSize",info,"next","prev","readList","pushButton","readCountAll","readCountRead");

searchButton.addEventListener("click",()=> searchButtonHandler(1));

let booksData = [];
let active = null;
let counter = null;
let readList = null;

readList = loadFromLocalStorage();
console.log(readList)
readList.forEach(item => {ui.readCountAll.innerText++;if(item.isRead) ui.readCountRead.innerText++; });
//console.log(readList);
ui.list.addEventListener("click",listHandler);
ui.next.addEventListener("click",()=>{counterHandler(1)});
ui.prev.addEventListener("click",()=>{counterHandler(-1)});
ui.pushButton.addEventListener("click",pushButtonHandler);

ui.toReadList.addEventListener("click",(e) => {
	if(e.target.className=="rightBlock-list__item-remove") removeButtonHandler(e);
	else if(e.target.className == "rightBlock-list__item-mark") markButtonHander(e);
});